:root {
  --moderate-cyan: hsl(176, 50%, 47%);
  --dark-cyan: hsl(176, 72%, 28%);
  --black: hsl(0, 0%, 0%);
  --dark-gray: hsl(0, 0%, 48%);
}

:root {
  --font-commissioner: "Commissioner", sans-serif;
}

body {
  font-family: var(--font-commissioner);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-commissioner);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

/* How to include mixin
@include breakpoint(large) {
   font-size: 3rem;
}
*/
nav {
  height: 500px;
  background: url(../../../images/image-hero-desktop.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  padding-left: 200px;
  padding-right: 200px;
  padding-top: 50px;
}
nav .nav__container {
  display: flex;
  justify-content: space-between;
}
nav .nav__container ul {
  display: flex;
  list-style: none;
}
nav .nav__container ul li a {
  color: white;
  margin-left: 2rem;
}